    video {
        object-fit: cover;
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: -1;
    }

    .hero-container { 
        /*background: url('https://i.imgur.com/Ei6gnQY.png') center center/cover no-repeat;*/
        /* Main background image goes here  ((IF NO VID)) ^^*/
        height: 100vh;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
        object-fit: contain;
    }
    
    .hero-container > h1 {
        color: #fff;
        font-size: 100px;
        margin-top: -100px;
        font-family: monospace;
        text-align: center;
    }

    .hero-container > p {
        margin-top: 8px;
        color: #fff;
        font-size: 32px;
        font-family: Georgia, 'Times New Roman', Times, serif;
        text-align: center;
        margin-bottom: 8px;

    }
 
    .hero-btns {
        margin-top: 32px;
    }
    
    .hero-btns .btn {
        margin-top: 6px;
    }

    @media screen and (max-width: 960px) {
        hero-container > h1 {
            font-size: 50px;
        }
    }

    @media screen and (max-width: 768px) {
        .hero-container > h1 {
          font-size: 50px;
          margin-top: -100px;
        }
      
        .hero-container > p {
          font-size: 30px;
        }
      
        .btn-mobile {
          display: block;
          text-decoration: none;
        }
      
        .btn {
          width: 100%;
        }
      }